html, body {
  @include font-smoothing;
  @include transition( opacity 0.3s ease-out );
  font-size: 62.5%;
  @include gotham-medium;
  color: $dark-grey;
  overflow-x: hidden;
}

*, *:before, *:after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

// Images
img.responsive-img,
video.responsive-video {
  max-width: 100%;
  height: auto;
}

.fade {
  opacity: 0;
  transform-origin: 0 50%;

  &.in {
    opacity: 1;
  }
}

// Responsive Videos
.video-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
  &.no-controls {
    padding-top: 0;
  }

  iframe, object, embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

// Variables

$contentWidth: 112rem;

// Global styles - project specific

.breadcrumbs {
    font-size: 1.5rem;
    @include gotham-book();
    margin: 0 0 3rem;

    a, a:hover, a:active, a:visited, a:link {
        color: $white;
        @include gotham-book();

        &.active {
            @include gotham-medium();
            letter-spacing: -0.48px;
        }

        &:hover {
            color: $blue;
        }
    }
}

::selection {
  background: $blue;
  color: $white;
  opacity: 1;
}

.overflow {
  overflow: hidden;
}
